import { APIError } from '../types/api';

interface ErrorLog {
  source: string;
  message: string;
  code: string;
  timestamp: string;
  resolutionTimestamp?: string;
  stackTrace?: string;
  details?: Record<string, unknown>;
}

export class ErrorLogger {
  private static readonly STORAGE_KEY = 'error_logs';
  private static readonly DB_NAME = 'errorLogs';
  private static readonly STORE_NAME = 'errors';
  
  public static async logError(error: APIError, source: string): Promise<void> {
    const errorLog: ErrorLog = {
      source,
      message: error.message,
      code: error.code,
      timestamp: new Date().toISOString(),
      stackTrace: new Error().stack,
      details: error.details
    };

    try {
      // Сначала пробуем использовать IndexedDB для постоянного хранения
      const db = await this.getIndexedDB();
      const transaction = db.transaction([this.STORE_NAME], 'readwrite');
      const store = transaction.objectStore(this.STORE_NAME);
      await store.add(errorLog);
    } catch (e) {
      // Если IndexedDB недоступен, используем localStorage
      const existingLogs = this.getLogsFromLocalStorage();
      existingLogs.push(errorLog);
      localStorage.setItem(this.STORAGE_KEY, JSON.stringify(existingLogs));
    }

    // Также можно отправить ошибку на бэкенд API
    try {
      await fetch('/api/error-logs', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(errorLog),
      });
    } catch (e) {
      console.error('Не удалось отправить лог ошибки на сервер:', e);
    }
  }

  public static async markResolved(errorId: string): Promise<void> {
    const resolutionTimestamp = new Date().toISOString();
    
    try {
      const db = await this.getIndexedDB();
      const transaction = db.transaction([this.STORE_NAME], 'readwrite');
      const store = transaction.objectStore(this.STORE_NAME);
      
      return new Promise((resolve, reject) => {
        const getRequest = store.get(errorId);
        
        getRequest.onsuccess = () => {
          const error = getRequest.result as ErrorLog;
          if (error) {
            error.resolutionTimestamp = resolutionTimestamp;
            const putRequest = store.put(error);
            putRequest.onsuccess = () => resolve();
            putRequest.onerror = () => reject(putRequest.error);
          } else {
            resolve();
          }
        };
        
        getRequest.onerror = () => reject(getRequest.error);
      });
    } catch (e) {
      // Резервное сохранение в localStorage
      const logs = this.getLogsFromLocalStorage();
      const errorIndex = logs.findIndex(log => log.timestamp === errorId);
      if (errorIndex !== -1) {
        logs[errorIndex].resolutionTimestamp = resolutionTimestamp;
        localStorage.setItem(this.STORAGE_KEY, JSON.stringify(logs));
      }
    }
  }

  private static getLogsFromLocalStorage(): ErrorLog[] {
    const logsString = localStorage.getItem(this.STORAGE_KEY);
    return logsString ? JSON.parse(logsString) : [];
  }

  private static async getIndexedDB(): Promise<IDBDatabase> {
    return new Promise((resolve, reject) => {
      try {
        const indexedDB = 
          globalThis.indexedDB ||
          (globalThis as any).mozIndexedDB ||
          (globalThis as any).webkitIndexedDB ||
          (globalThis as any).msIndexedDB;

        if (!indexedDB) {
          throw new Error('IndexedDB не поддерживается в этом браузере');
        }

        const openRequest = indexedDB.open(this.DB_NAME, 1);

        openRequest.onerror = () => reject(openRequest.error);
        openRequest.onsuccess = () => resolve(openRequest.result);

        openRequest.onupgradeneeded = () => {
          const db = openRequest.result;
          if (!db.objectStoreNames.contains(this.STORE_NAME)) {
            db.createObjectStore(this.STORE_NAME, { keyPath: 'timestamp' });
          }
        };
      } catch (error) {
        reject(error);
      }
    });
  }
}