// src/components/personal/sections/ClientsSection/ClientsSection.tsx
import React, { useState } from 'react';
import { useClientData } from './hooks/useClientsData';
import { useClientFilters } from './hooks/useClientFilters';
import { useClientStatus } from './hooks/useClientStatus';
import { TEXT_STYLES, COMPONENT_STYLES } from '@/theme';
import { ClientFilters, Client } from './types';

import Toolbar from './components/Toolbar/Toolbar';
import Table from './components/Table/Table';
import ClientCard from './components/ClientCard/ClientCard';
import StatusSettings from './components/StatusSettings/StatusSettings';
import { Alert } from '@/components/ui/alert';

interface ClientsSectionProps {
  initialClients?: Client[];
}

const ClientsSection: React.FC<ClientsSectionProps> = ({ initialClients = [] }) => {
  const {
    clients,
    selectedClientId,
    selectClient,
    updateClientDiscount,
    exportClientsToCSV,
    isLoading: isClientsLoading
  } = useClientData(initialClients);

  const {
    filters,
    searchQuery,
    showFilters,
    filteredClients,
    updateFilters,
    setSearchQuery,
    toggleFilters,
    isFiltering
  } = useClientFilters(clients);

  const {
    statusSettings,
    showStatusSettings,
    clientStats,
    autoCalculationRequirements,
    loading: isStatusLoading,
    error: statusError,
    modalError, // Новое состояние для ошибок модального окна
    toggleStatusSettings,
    updateStatusSettings,
    recalculateClientStatuses,
    averageLoyalInterval,
    averageRegularInterval
  } = useClientStatus(clients);

  const [showExtended, setShowExtended] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [actionInProgress, setActionInProgress] = useState(false);

  const handleClientSelect = (clientId: string) => {
    selectClient(clientId);
  };

  const handleClientCardClose = () => {
    selectClient(null);
  };

  const handleClientDiscountSave = async (clientId: string, discount: number) => {
    setActionInProgress(true);
    try {
      await updateClientDiscount(clientId, discount);
      selectClient(null);
    } finally {
      setActionInProgress(false);
    }
  };

  const handleStatusSettingsSave = async () => {
    setActionInProgress(true);
    try {
      if (statusSettings) {
        await updateStatusSettings(statusSettings);
        await recalculateClientStatuses();
      }
    } finally {
      setActionInProgress(false);
    }
  };

  const handleFiltersChange = (newFilters: ClientFilters) => {
    updateFilters(newFilters);
    setCurrentPage(1);
  };

  const handleShowExtendedChange = (show: boolean) => {
    setShowExtended(show);
    setCurrentPage(1);
  };

  const handleExport = async () => {
    setActionInProgress(true);
    try {
      await exportClientsToCSV(filteredClients);
    } finally {
      setActionInProgress(false);
    }
  };

  const selectedClient = clients.find(client => client.id === selectedClientId);

  const isLoading = isClientsLoading || isStatusLoading || actionInProgress;
  const hasError = Boolean(statusError) && !showStatusSettings; // Показываем ошибку только если модальное окно закрыто
  const canInteract = !isLoading && !hasError;

  return (
    <div className={`space-y-6 ${TEXT_STYLES.body}`}>
      {isLoading && (
        <Alert variant="loading" className="mb-4">
          Загрузка данных...
        </Alert>
      )}

      {hasError && (
        <Alert variant="destructive" className="mb-4">
          {statusError?.message || 'Произошла ошибка при загрузке данных'}
        </Alert>
      )}

      <Toolbar
        searchQuery={searchQuery}
        onSearchChange={setSearchQuery}
        showFilters={showFilters}
        onToggleFilters={toggleFilters}
        onExport={handleExport}
        onOpenSettings={toggleStatusSettings}
        filters={filters}
        onFiltersChange={handleFiltersChange}
        showExtended={showExtended}
        onShowExtendedChange={handleShowExtendedChange}
        disabled={!canInteract}
        isFiltering={isFiltering}
      />
      
      <div className={COMPONENT_STYLES.card.base}>
        <Table
          clients={clients}
          filteredClients={filteredClients}
          showExtended={showExtended}
          onClientSelect={handleClientSelect}
          currentPage={currentPage}
          pageSize={pageSize}
          onPageChange={setCurrentPage}
          onPageSizeChange={setPageSize}
          loading={isLoading}
          error={hasError ? statusError : null} // Передаем ошибку только если нет модального окна
          stats={{
            averageLoyalInterval,
            averageRegularInterval,
            ...clientStats
          }}
        />
      </div>

      {selectedClient && (
        <ClientCard
          client={selectedClient}
          onClose={handleClientCardClose}
          onSave={handleClientDiscountSave}
          disabled={!canInteract}
        />
      )}

      {showStatusSettings && statusSettings && (
        <StatusSettings
          isOpen={showStatusSettings}
          onClose={toggleStatusSettings}
          settings={statusSettings}
          stats={clientStats}
          error={modalError} // Используем отдельное состояние для ошибок модального окна
          loading={isStatusLoading}
          onSettingsChange={updateStatusSettings}
          onSave={handleStatusSettingsSave}
          autoCalculationRequirements={autoCalculationRequirements}
        />
      )}

      {actionInProgress && (
        <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center">
          <div className="bg-white p-6 rounded-lg shadow-xl">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-green-600"></div>
          </div>
        </div>
      )}

      {!isLoading && !hasError && clients.length === 0 && (
        <div className={`${COMPONENT_STYLES.card.base} p-6 text-center`}>
          <p className={TEXT_STYLES.body}>Нет данных для отображения</p>
        </div>
      )}
    </div>
  );
};

export default ClientsSection;