// src/components/personal/sections/ClientsSection/utils/api.ts
import { APIError, APIResponse } from '../types/api';
import { ErrorLogger } from './error-logger';

export class APIClient {
  private static baseUrl = '/api';

  private static async handleResponse<T>(response: Response, endpoint: string): Promise<APIResponse<T>> {
    if (!response.ok) {
      let error: APIError;
      try {
        // Use type assertion to handle unknown type
        const errorData = await response.json() as Record<string, unknown>;
        error = {
          message: typeof errorData.message === 'string' 
            ? errorData.message 
            : 'An unexpected error occurred',
          code: typeof errorData.code === 'string' 
            ? errorData.code 
            : 'UNKNOWN_ERROR',
          details: typeof errorData === 'object' ? errorData : undefined
        };
        
        // Логируем ошибку ответа
        await ErrorLogger.logError(error, `api.ts:handleResponse - ${endpoint} - ${response.status}`);
      } catch {
        error = {
          message: 'An unexpected error occurred',
          code: 'UNKNOWN_ERROR'
        };
        // Логируем ошибку парсинга
        await ErrorLogger.logError(error, `api.ts:handleResponse - Parse Error - ${endpoint}`);
      }
      return { error };
    }

    const data = await response.json() as T;
    return { data };
  }

  static async get<T>(endpoint: string): Promise<APIResponse<T>> {
    try {
      const response = await fetch(`${this.baseUrl}${endpoint}`);
      return this.handleResponse<T>(response, endpoint);
    } catch (error) {
      const apiError = {
        message: 'Network error occurred',
        code: 'NETWORK_ERROR',
        details: {
          endpoint,
          method: 'GET',
          error: error instanceof Error ? error.message : 'Unknown error'
        }
      };
      
      // Логируем ошибку сети
      await ErrorLogger.logError(apiError, `api.ts:get - Network Error - ${endpoint}`);
      
      return { error: apiError };
    }
  }

  static async post<T>(endpoint: string, data: unknown): Promise<APIResponse<T>> {
    try {
      const response = await fetch(`${this.baseUrl}${endpoint}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });
      return this.handleResponse<T>(response, endpoint);
    } catch (error) {
      const apiError = {
        message: 'Network error occurred',
        code: 'NETWORK_ERROR',
        details: {
          endpoint,
          method: 'POST',
          data,
          error: error instanceof Error ? error.message : 'Unknown error'
        }
      };
      
      // Логируем ошибку сети
      await ErrorLogger.logError(apiError, `api.ts:post - Network Error - ${endpoint}`);
      
      return { error: apiError };
    }
  }
}