// src/components/personal/sections/ClientsSection/hooks/useClientStatus.ts
import { useState, useCallback, useMemo, useEffect } from 'react';
import type { 
  Client,
  StatusSettingsType,
  ClientStats,
  APIError,
  APIStatusSettings
} from '../types';
import { APIClient } from '../utils/api';
import { displayErrorNotification } from '../utils/error-handlers';

const defaultSettings: StatusSettingsType = {
  manual: {
    ordersX: 2,
    ordersY: 4,
    ordersZ: 5,
    intervalA: 21,
    intervalB: 11,
    daysC: 51
  },
  useAutoCalculation: false
};

interface UseClientStatusState {
  statusSettings: StatusSettingsType;
  loading: boolean;
  error: APIError | null;
  modalError: APIError | null; // Добавляем отдельное состояние для ошибок модального окна
  clientStats: ClientStats;
  autoCalculationRequirements: {
    MIN_REGULAR_CLIENTS: number;
    MIN_LOYAL_CLIENTS: number;
  };
  lastSuccessfulSettings: StatusSettingsType | null;
}

const initialState: UseClientStatusState = {
  statusSettings: defaultSettings,
  loading: false,
  error: null,
  modalError: null, // Инициализируем новое состояние
  clientStats: {
    cold: 0,
    new: 0,
    regular: 0,
    loyal: 0,
    lost: 0
  },
  autoCalculationRequirements: {
    MIN_REGULAR_CLIENTS: 20,
    MIN_LOYAL_CLIENTS: 5
  },
  lastSuccessfulSettings: null
};

const calculateClientStats = (clients: Client[]): ClientStats => ({
  cold: clients.filter(c => c.status === 'cold').length,
  new: clients.filter(c => c.status === 'new').length,
  regular: clients.filter(c => c.status === 'regular').length,
  loyal: clients.filter(c => c.status === 'loyal').length,
  lost: clients.filter(c => c.status === 'lost').length
});

const transformAPISettings = (apiSettings: APIStatusSettings): StatusSettingsType => ({
  manual: {
    ordersX: apiSettings.manual_settings.ordersX || defaultSettings.manual.ordersX,
    ordersY: apiSettings.manual_settings.ordersY || defaultSettings.manual.ordersY,
    ordersZ: apiSettings.manual_settings.ordersZ || defaultSettings.manual.ordersZ,
    intervalA: apiSettings.manual_settings.intervalA || defaultSettings.manual.intervalA,
    intervalB: apiSettings.manual_settings.intervalB || defaultSettings.manual.intervalB,
    daysC: apiSettings.manual_settings.daysC || defaultSettings.manual.daysC
  },
  useAutoCalculation: apiSettings.use_auto_calculation
});

const transformToAPISettings = (settings: StatusSettingsType): APIStatusSettings => ({
  manual_settings: settings.manual,
  use_auto_calculation: settings.useAutoCalculation
});

export const useClientStatus = (clients: Client[]) => {
  const [state, setState] = useState<UseClientStatusState>(initialState);
  const [showStatusSettings, setShowStatusSettings] = useState(false);

  const fetchStatusSettings = useCallback(async () => {
    setState(prev => ({
      ...prev,
      loading: true,
      modalError: null // Сбрасываем ошибку модального окна при новом запросе
    }));
    
    try {
      const response = await APIClient.get<APIStatusSettings>('/status-settings/');
      
      if (response.error) {
        setState(prev => ({
          ...prev,
          loading: false,
          modalError: { // Устанавливаем ошибку только для модального окна
            message: 'Ошибка загрузки данных, попробуйте позже',
            code: 'FETCH_ERROR'
          },
          statusSettings: prev.lastSuccessfulSettings || prev.statusSettings
        }));
        return;
      }

      if (response.data) {
        const transformedSettings = transformAPISettings(response.data);
        setState(prev => ({
          ...prev,
          loading: false,
          modalError: null,
          statusSettings: transformedSettings,
          lastSuccessfulSettings: transformedSettings,
          clientStats: calculateClientStats(clients)
        }));
        return transformedSettings;
      }

      throw new Error('Не удалось получить данные с сервера');

    } catch (error) {
      setState(prev => ({
        ...prev,
        loading: false,
        modalError: { // Устанавливаем ошибку только для модального окна
          message: 'Ошибка загрузки данных, попробуйте позже',
          code: 'UNKNOWN_ERROR'
        },
        statusSettings: prev.lastSuccessfulSettings || prev.statusSettings
      }));
    }
  }, [clients]);

  const updateStatusSettings = useCallback(async (newSettings: Partial<StatusSettingsType>) => {
    setState(prev => ({
      ...prev,
      loading: true,
      modalError: null
    }));
    
    try {
      const currentSettings = state.statusSettings;
      const mergedSettings: StatusSettingsType = {
        ...currentSettings,
        ...newSettings
      };

      const apiSettings = transformToAPISettings(mergedSettings);
      const response = await APIClient.post<APIStatusSettings>('/status-settings/', apiSettings);
      
      if (response.error) {
        setState(prev => ({
          ...prev,
          loading: false,
          modalError: {
            message: 'Ошибка загрузки данных, попробуйте позже',
            code: 'FETCH_ERROR'
          },
          statusSettings: prev.lastSuccessfulSettings || prev.statusSettings
        }));
        return false;
      }

      if (response.data) {
        const transformedSettings = transformAPISettings(response.data);
        setState(prev => ({
          ...prev,
          loading: false,
          modalError: null,
          statusSettings: transformedSettings,
          lastSuccessfulSettings: transformedSettings
        }));
        return true;
      }

      throw new Error('Не удалось сохранить настройки');

    } catch (error) {
      setState(prev => ({
        ...prev,
        loading: false,
        modalError: {
          message: 'Ошибка загрузки данных, попробуйте позже',
          code: 'UNKNOWN_ERROR'
        },
        statusSettings: prev.lastSuccessfulSettings || prev.statusSettings
      }));
      return false;
    }
  }, [state.statusSettings]);

  const recalculateClientStatuses = useCallback(async () => {
    setState(prev => ({
      ...prev,
      loading: true,
      error: null
    }));
    
    try {
      const response = await APIClient.post<Client[]>('/clients/update-statuses/', clients);
      
      if (response.error) {
        setState(prev => ({
          ...prev,
          loading: false,
          error: {
            message: 'Ошибка загрузки данных, попробуйте позже',
            code: 'FETCH_ERROR'
          }
        }));
        displayErrorNotification(response.error);
        return null;
      }

      if (response.data) {
        const updatedClients = response.data;
        setState(prev => ({
          ...prev,
          loading: false,
          error: null,
          clientStats: calculateClientStats(updatedClients)
        }));
        return updatedClients;
      }

      throw new Error('Не удалось обновить статусы клиентов');

    } catch (error) {
      setState(prev => ({
        ...prev,
        loading: false,
        error: {
          message: 'Ошибка загрузки данных, попробуйте позже',
          code: 'UNKNOWN_ERROR'
        }
      }));
      displayErrorNotification({
        message: 'Ошибка загрузки данных, попробуйте позже',
        code: 'UNKNOWN_ERROR'
      });
      return null;
    }
  }, [clients]);

  const toggleStatusSettings = useCallback(() => {
    const newShowStatus = !showStatusSettings;
    setShowStatusSettings(newShowStatus);
    
    if (newShowStatus) {
      fetchStatusSettings();
    } else {
      // Сбрасываем ошибку модального окна при его закрытии
      setState(prev => ({
        ...prev,
        modalError: null
      }));
    }
  }, [showStatusSettings, fetchStatusSettings]);

  const checkAutoCalculationPossibility = useCallback((): boolean => {
    return state.clientStats.regular >= state.autoCalculationRequirements.MIN_REGULAR_CLIENTS &&
           state.clientStats.loyal >= state.autoCalculationRequirements.MIN_LOYAL_CLIENTS;
  }, [state.clientStats, state.autoCalculationRequirements]);

  const averageLoyalInterval = useMemo(() => {
    const loyalClients = clients.filter(c => c.status === 'loyal');
    if (!loyalClients.length) return 0;
    return Math.floor(loyalClients.reduce((acc, c) => acc + c.averageInterval, 0) / loyalClients.length);
  }, [clients]);

  const averageRegularInterval = useMemo(() => {
    const regularClients = clients.filter(c => c.status === 'regular');
    if (!regularClients.length) return 0;
    return Math.floor(regularClients.reduce((acc, c) => acc + c.averageInterval, 0) / regularClients.length);
  }, [clients]);

  useEffect(() => {
    setState(prev => ({
      ...prev,
      clientStats: calculateClientStats(clients)
    }));
  }, [clients]);

  return {
    ...state,
    showStatusSettings,
    averageLoyalInterval,
    averageRegularInterval,
    toggleStatusSettings,
    updateStatusSettings,
    recalculateClientStatuses,
    checkAutoCalculationPossibility
  };
};

export default useClientStatus;