// src/components/personal/sections/ClientsSection/components/StatusSettings/StatusSettings.tsx
import React, { useState, useEffect, useCallback } from 'react';
import { X } from 'lucide-react';
import { 
  StatusSettingsType, 
  ClientStats, 
  AutoCalculationRequirements,
  APIError
} from '../../types';
import { TEXT_STYLES, COMPONENT_STYLES } from '@/theme';

// Declare the confirm method on the global window object type
declare global {
  interface Window {
    confirm(message?: string): boolean;
  }
}

const EXTENDED_COMPONENT_STYLES = {
  ...COMPONENT_STYLES,
  card: {
    ...COMPONENT_STYLES.card,
    footer: 'px-6 py-4 border-t border-[#A7ABAA]/20'
  },
  input: {
    ...COMPONENT_STYLES.input,
    checkbox: 'h-4 w-4 rounded border-gray-300 text-primary focus:ring-primary'
  }
};

interface StatusSettingsProps {
  isOpen: boolean;
  onClose: () => void;
  settings: StatusSettingsType;
  stats: ClientStats;
  error: APIError | null;
  loading: boolean;
  autoCalculationRequirements: AutoCalculationRequirements;
  onSettingsChange: (settings: StatusSettingsType) => void;
  onSave: () => Promise<void>;
}

type InputField = {
  id: keyof StatusSettingsType['manual'];
  label: string;
  value: number;
};

const calculateButtonState = (
  isLoading: boolean,
  hasChanges: boolean
): boolean => {
  return isLoading || !hasChanges;
};

export const StatusSettings: React.FC<StatusSettingsProps> = ({
  isOpen,
  onClose,
  settings,
  stats,
  error,
  loading,
  autoCalculationRequirements,
  onSettingsChange,
  onSave
}) => {
  const [localSettings, setLocalSettings] = useState<StatusSettingsType>(settings);
  const [hasChanges, setHasChanges] = useState<boolean>(false);
  const [isModalVisible, setIsModalVisible] = useState<boolean>(isOpen);
  const [isSaving, setIsSaving] = useState<boolean>(false);

  useEffect(() => {
    if (isOpen) {
      setLocalSettings(settings);
      setHasChanges(false);
      setIsSaving(false);
      setIsModalVisible(true);
    }
  }, [isOpen, settings]);

  const handleSafeClose = useCallback(() => {
    // Use optional chaining and type guard for window
    if (!hasChanges || (typeof window !== 'undefined' && window.confirm('Есть несохраненные изменения. Закрыть?'))) {
      setIsModalVisible(false);
      onClose();
    }
  }, [hasChanges, onClose]);

  const handleBackdropClick = useCallback((e: React.MouseEvent<HTMLDivElement>) => {
    if (e.target === e.currentTarget) {
      handleSafeClose();
    }
  }, [handleSafeClose]);

  const handleSettingChange = useCallback((
    field: keyof StatusSettingsType['manual'],
    value: number
  ) => {
    setLocalSettings(prev => ({
      ...prev,
      manual: {
        ...prev.manual,
        [field]: Math.max(0, value)
      }
    }));
    setHasChanges(true);
  }, []);

  const handleAutoCalculationToggle = useCallback(() => {
    setLocalSettings(prev => ({
      ...prev,
      useAutoCalculation: !prev.useAutoCalculation
    }));
    setHasChanges(true);
  }, []);

  const handleInputChange = useCallback((
    e: React.ChangeEvent<HTMLInputElement>,
    field: keyof StatusSettingsType['manual']
  ) => {
    // Explicit type assertion and null check
    const inputValue = e.target.value;
    const value = Math.max(0, parseInt(inputValue || '0', 10));
    handleSettingChange(field, value);
  }, [handleSettingChange]);

  const handleSave = async () => {
    if (isSaving || !hasChanges) return;

    setIsSaving(true);

    try {
      onSettingsChange(localSettings);
      await onSave();
      setHasChanges(false);
      handleSafeClose();
    } catch (err) {
      console.error('Ошибка при сохранении:', err);
    } finally {
      setIsSaving(false);
    }
  };

  const inputFields: InputField[] = [
    { id: 'ordersX', label: 'Мин. заказов (постоянный)', value: localSettings.manual.ordersX },
    { id: 'ordersY', label: 'Макс. заказов (постоянный)', value: localSettings.manual.ordersY },
    { id: 'ordersZ', label: 'Мин. заказов (лояльный)', value: localSettings.manual.ordersZ },
    { id: 'intervalA', label: 'Интервал (дни)', value: localSettings.manual.intervalA },
    { id: 'intervalB', label: 'Интервал лояльности (дни)', value: localSettings.manual.intervalB },
    { id: 'daysC', label: 'Дней до потери', value: localSettings.manual.daysC }
  ];

  if (!isModalVisible) return null;

  const isButtonDisabled = calculateButtonState(
    isSaving || loading, 
    hasChanges
  );

  return (
    <div 
      className="fixed inset-0 bg-black/50 z-50 flex items-center justify-center"
      onClick={handleBackdropClick}
    >
      <div className={`${EXTENDED_COMPONENT_STYLES.card.base} w-full max-w-xl m-4`}>
        <div className={`${EXTENDED_COMPONENT_STYLES.card.header} flex justify-between items-center`}>
          <h2 className={TEXT_STYLES.h2}>Настройка статусов клиентов</h2>
          <button 
            type="button"
            onClick={handleSafeClose}
            className="h-6 w-6 p-0 hover:bg-neutral-100 rounded-full flex items-center justify-center"
            aria-label="Закрыть"
          >
            <X className="h-4 w-4" />
          </button>
        </div>

        <div className={EXTENDED_COMPONENT_STYLES.card.body}>
          {loading ? (
            <div className="flex justify-center items-center p-8">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-green-600"></div>
            </div>
          ) : error ? (
            <div className="bg-red-50 p-4 rounded-md">
              <p className="text-red-600 text-center">Ошибка загрузки данных, попробуйте позже</p>
            </div>
          ) : (
            <>
              <div className="flex items-center justify-between mb-6">
                <label 
                  htmlFor="auto-calc"
                  className={TEXT_STYLES.body}
                >
                  Использовать автоматический расчет
                </label>
                <input
                  id="auto-calc"
                  type="checkbox"
                  checked={localSettings.useAutoCalculation}
                  onChange={handleAutoCalculationToggle}
                  className={EXTENDED_COMPONENT_STYLES.input.checkbox}
                  disabled={loading}
                />
              </div>

              <div className="grid grid-cols-2 gap-4 mb-6">
                {inputFields.map(({ id, label, value }) => (
                  <div key={id} className="space-y-2">
                    <label htmlFor={id} className={TEXT_STYLES.body}>
                      {label}
                    </label>
                    <input
                      id={id}
                      type="number"
                      value={value}
                      onChange={(e) => handleInputChange(e, id)}
                      disabled={localSettings.useAutoCalculation || loading}
                      min="0"
                      className={`${EXTENDED_COMPONENT_STYLES.input.base} w-full`}
                      aria-label={label}
                    />
                  </div>
                ))}
              </div>

              <div className={`${EXTENDED_COMPONENT_STYLES.card.base} p-6`}>
                <h3 className={TEXT_STYLES.h3}>Статистика клиентов</h3>
                <div className="grid grid-cols-2 gap-4 mt-4">
                  {Object.entries(stats).map(([key, value]) => (
                    <div key={key} className="flex justify-between items-center">
                      <span className={`${TEXT_STYLES.body} capitalize`}>{key}</span>
                      <span className={TEXT_STYLES.body}>{value}</span>
                    </div>
                  ))}
                </div>
              </div>

              {!localSettings.useAutoCalculation && (
                <div className="mt-4 text-sm text-gray-500">
                  Для включения автоматического расчета необходимо минимум{' '}
                  {autoCalculationRequirements.MIN_REGULAR_CLIENTS} постоянных и{' '}
                  {autoCalculationRequirements.MIN_LOYAL_CLIENTS} лояльных клиентов
                </div>
              )}
            </>
          )}
        </div>

        <div className={`${EXTENDED_COMPONENT_STYLES.card.footer} flex justify-end space-x-4`}>
          <button
            type="button"
            onClick={handleSafeClose}
            className={`${EXTENDED_COMPONENT_STYLES.button.base} ${EXTENDED_COMPONENT_STYLES.button.secondary}`}
            disabled={isSaving}
          >
            Отмена
          </button>
          <button
            type="button"
            onClick={handleSave}
            disabled={isButtonDisabled}
            className={`${EXTENDED_COMPONENT_STYLES.button.base} ${EXTENDED_COMPONENT_STYLES.button.primary}`}
          >
            {isSaving ? 'Сохранение...' : 'Сохранить'}
          </button>
        </div>
      </div>
    </div>
  );
};

export default StatusSettings;