// src/components/ui/alert.tsx
import React from 'react';
import { COLORS } from '@/theme/colors';
import { TEXT_STYLES } from '@/theme/typography';

interface AlertProps {
  children: React.ReactNode;
  variant?: 'default' | 'destructive' | 'loading';
  className?: string;
}

export const Alert = ({ 
  children, 
  variant = 'default',
  className = '' 
}: AlertProps) => {
  const getVariantStyles = () => {
    switch (variant) {
      case 'destructive':
        return `bg-[${COLORS.status.error.light}] text-[${COLORS.status.error.dark}]`;
      case 'loading':
        return `bg-[${COLORS.status.info.light}] text-[${COLORS.status.info.dark}]`;
      default:
        return `bg-[${COLORS.neutral[50]}] text-[${COLORS.neutral[700]}]`;
    }
  };

  return (
    <div 
      className={`
        p-4 
        mb-4
        rounded-lg 
        border 
        ${getVariantStyles()}
        ${className}
        ${TEXT_STYLES.body}
      `}
    >
      {children}
    </div>
  );
};